import { createRouter, createWebHistory } from "vue-router";
import GermanTankReconFennek from "../components/germanTank/componentGermanTankRecon/tankCard/GermanTankReconFennek.vue";
import GermanTankReconLuchs from "../components/germanTank/componentGermanTankRecon/tankCard/GermanTankReconLuchs.vue";
import GermanTankReconSchuetzenpanzerKurz from "../components/germanTank/componentGermanTankRecon/tankCard/GermanTankReconSchuetzenpanzerKurz.vue";
import MainGermanReconTank from "../components/germanTank/componentGermanTankRecon/mainTankCategory/MainGermanReconTank.vue";

import LeopardOne from "../components/germanTank/componentMainBattleTank/tankCard/LeopardOne.vue";
import KampfpanzerSeventy from "../components/germanTank/componentMainBattleTank/tankCard/KampfpanzerSeventy.vue";
import LeopardTwo from "../components/germanTank/componentMainBattleTank/tankCard/LeopardTwo.vue";
import MainGermanBattleTankCategory from "../components/germanTank/componentMainBattleTank/mainTankCategory/MainGermanBattleTankCategory.vue";

import Jaguar_1 from "../components/germanTank/componentGermanTankDestroyer/tankCard/Jaguar_1.vue";
import Jaguar_2 from "../components/germanTank/componentGermanTankDestroyer/tankCard/Jaguar_2.vue";
import VT1 from "../components/germanTank/componentGermanTankDestroyer/tankCard/VT1.vue";
import HS_30 from "../components/germanTank/componentGermanTankDestroyer/tankCard/HS_30.vue";
import Kanonen_Jagdpanzer_1 from "../components/germanTank/componentGermanTankDestroyer/tankCard/Kanonen_Jagdpanzer_1.vue";
import Raketen_Jagdpanzer_2 from "../components/germanTank/componentGermanTankDestroyer/tankCard/Raketen_Jagdpanzer_2.vue";

import MainGermanDestroyerTank from "../components/germanTank/componentGermanTankDestroyer/mainTankCategory/MainGermanDestroyerTank.vue";

const routes = [
  //Main Route
  //Category Cards
  {
    path: "/germanTank/componentGermanTankRecon/mainTankCategory/MainGermanReconTank",
    name: "MainGermanReconTank",
    component: MainGermanReconTank,
    children: [
      //Recon Tank Cards
      {
        path: "/germanTankRecon/componentGermanTankRecon/tankCard/GermanTankReconFennek",
        name: "GermanTankReconFennek",
        component: GermanTankReconFennek,
        alias: "/",
      },
      {
        path: "/germanTankRecon/componentGermanTankRecon/tankCard/GermanTankReconLuchs",
        name: "GermanTankReconLuchs",
        component: GermanTankReconLuchs,
      },
      {
        path: "/germanTankRecon/componentGermanTankRecon/tankCard/GermanTankReconSchuetzenpanzerKurz",
        name: "GermanTankReconSchuetzenpanzerKurz",
        component: GermanTankReconSchuetzenpanzerKurz,
      },
    ],
  },
  {
    path: "/germanTank/componentMainBattleTank/mainTankCategory/MainGermanBattleTankCategory",
    name: "MainGermanBattleTankCategory",
    component: MainGermanBattleTankCategory,
    children: [
      //Battle Tank Cards
      {
        path: "/germanTank/componentMainBattleTank/tankCard/LeopardOne",
        name: "LeopardOne",
        component: LeopardOne,
        alias: "/",
      },
      {
        path: "/germanTank/componentMainBattleTank/tankCard/KampfpanzerSeventy",
        name: "KampfpanzerSeventy",
        component: KampfpanzerSeventy,
      },
      {
        path: "/germanTank/componentMainBattleTank/tankCard/LeopardTwo",
        name: "LeopardTwo",
        component: LeopardTwo,
      },
    ],
  },
  {
    path: "/germanTank/componentGermanTankDestroyer/mainTankCategory/MainGermanDestroyerTank",
    name: "MainGermanDestroyerTank",
    component: MainGermanDestroyerTank,
    children: [
      //Destroyer Tank Cards

      {
        path: "/germanTank/componentGermanTankDestroyer/tankCard/Jaguar_1",
        name: "Jaguar_1",
        component: Jaguar_1,
      },
      {
        path: "/germanTank/componentGermanTankDestroyer/tankCard/Jaguar_2",
        name: "Jaguar_2",
        component: Jaguar_2,
      },
      {
        path: "/germanTank/componentGermanTankDestroyer/tankCard/VT1",
        name: "VT1",
        component: VT1,
      },
      {
        path: "/germanTank/componentGermanTankDestroyer/tankCard/HS_30",
        component: HS_30,
      },
      {
        path: "/germanTank/componentGermanTankDestroyer/tankCard/Kanonen_Jagdpanzer_1",
        component: Kanonen_Jagdpanzer_1,
      },
      {
        path: "/germanTank/componentGermanTankDestroyer/tankCard/Raketen_Jagdpanzer_2",
        component: Raketen_Jagdpanzer_2,
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
